import React from "react";

import "./styles.scss";

const test_key = "pk_test_51MbzZWBLdrhwtT8MCEFOcegHqA4Uo3nJQvse4zsnSA04XHPdxzhkMA2iTJONOGriPYbxIEnpb0AcQuNd2Eq67A8K00geRsiPb5",
    live_key = "pk_live_51MbzZWBLdrhwtT8MQVnLB1gU0MwJOrOtPZDvLSoUNHryAm0P1je9wct7pWxtNmEhhxtOazi64BrcuF7jptOvZnVj00fSmORI80";

const StripeProduct = (props) => {
    const {id, link, test} = props;
    if (link) {

    }

    const product = {
        price: id.replaceAll('\\', ''),
        quantity: 1,
    };

    const checkoutOptions = {
        mode: "payment",
        lineItems: [product],
        successUrl: `https://anthonyandmaiwang.github.io/lebirb/success`,
        cancelUrl: `https://anthonyandmaiwang.github.io/lebirb/products`,
    }

    const redirectToCheckout = async () => {
        const key = Boolean(test) === true ? test_key : live_key;
        const {loadStripe} = await import("@stripe/stripe-js");
        const stripe = await loadStripe(key);

        await stripe.redirectToCheckout(checkoutOptions);
    };


    return (
        <div>
            {props.children}
            <button onClick={redirectToCheckout}>Buy Water Bottle</button>
        </div>
    );
};

export default StripeProduct;